@import "./normalize.css";
:root {
  --color-text: #666;
  --color-title: #333;
  --color-high-text: #ff5777;
  --color-tint: #ff8198;
  --color-background: #fff;
  --color-border-bottom: #dce4ec;
  --color-low: #5ea732;
  --font-size: 14px;
  --line-height: 1.5;
}
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  user-select: none; /* user selecting img not allowed */
  -webkit-tap-highlight-color: transparent; /* webkit apple phone */
  background: var(--color-background);
  color: var(--color-text);
  width: 100%;
  height: 100%;
}
a {
  color: var(--color-text);
  text-decoration: none;
}
